<template>
	<div class="modal-layout">
		<div class="darken">
			<div class="new-modal add-crypto-starter-modal">
				<button class="new-modal-back-button" @click="closeModal"><img :src="require('@/assets/newImages/white_cross.png')"></button>
				<div class="new-modal-content">
					<div class="new-modal-header"><img :src="require('@/assets/newImages/cryptostarter.svg')"><h3 class="new-modal-title">{{ $localizeFilter('crypto_starter_modal_title') }}</h3></div>
					<label class="new-modal-text">{{ $localizeFilter('crypto_starter_modal_text') }}</label>
					<input v-model="result" :placeholder="$localizeFilter('add_goal_amount')" type="number" class="new-modal-input" :class="{ invalid: v$.result.$error }">
					 <small class="error" v-for="(error, index) of v$.result.$errors" :key="index">
						{{ error.$message }}
					</small>
					<div v-if="result > 0" class="result">{{ $localizeFilter('result_crypto_start',[   `${$utils.formatNumber(resultValue(), 'en-US', 6, 6)}`]) }}%</div>
					<button class="basic-button" @click="submitHandler()">{{ $localizeFilter('buy') }}</button>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'
import localizeFilter from '@/filters/localize.filter'
import useVuelidate from "@vuelidate/core";
import { helpers, required, numeric, minValue } from "@vuelidate/validators";

export default defineComponent({
	data() {
		return {
			rateFenix: 0.9,
			result: '',
			text: 'USDT',
			minAmount: 0,
		}
	},
	setup() {
        return { v$: useVuelidate() };
    },
	validations() {
        return {
            result: {
                required: helpers.withMessage(localizeFilter("field_must_not_be_empty"), required),
                numeric: helpers.withMessage(localizeFilter("field_must_be_numeric"), required),
                minValue: helpers.withMessage(localizeFilter('min_value', [this.minAmount]), minValue(this.minAmount)),
            },
        };
    },
	methods: {
		closeModal(reload) {
			this.$emit('closeModal', reload)
		},
		resultValue() {
			return this.result / (100000000 * this.rateFenix) * 100
		},
		submitHandler() {
			this.v$.$touch();
            if (this.v$.$error) {
                return;
            }
			this.$emit('continueBuying', this.resultValue)
		}
   }
})
</script>

