            <template v-if="auctionRunning">
                <div class="auction-running-text-1" v-html="$localizeFilter('cryptostart_closed_auction_running_text_1')"></div>

                <div class="coin-timer-block">
                    <p class="timer" :class="'auction-running'">{{ timeLeft }}</p>
                </div>

                <ul class="content-table">
                    <li class="content-line">
                        <div class="content-cell-1">
                            <p class="text" v-html="$localizeFilter('closed_auction_share_size')"></p>
                        </div>
                        <div class="content-cell-2">
